.ind-apartment {
    margin: 9rem;
}

.ind-apartment-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 30vw;
    padding: 2rem;
    opacity: .8;
}

.ind-apartment-info h2 {
    font-size: 30px;
    font-weight: 300;
    line-height: 30px;
    margin-bottom: 8px;
}

.apartment-details {
    display: flex;
}

.apartment-details h4 {
    border-right: solid 1px black;
    margin-right: 0.5rem;
    padding-right: 0.5rem;
}
.background-image {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

 .rent-price {
     background-color: #f4f4f4;
     display: flex;
     justify-content: space-between;
     width: 100%;
     flex-wrap: wrap;
}

.header-rent-price {
    margin-top: 0rem;
    background: #cb957c;
    box-shadow: 0px 8px 16px 0px rgb(186 183 183);
    height: 2rem;
    margin-left: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 2rem;
    opacity: .8;
    width: 30vw;
    margin-bottom: 0;
}
.ind-apartment-buttons {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

button {
    padding: 1rem;
    background: lightblue;
    border: none;
    margin: 0.5rem;
    width: 25vw;
}

button:hover {
    cursor: pointer;
}

.ind-apartment-top {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.navigation {
    display: flex;
    margin-top: 1;
    align-items: center;
    justify-content: flex-start;
    margin-left: 9vw;
    margin-top: 10rem;
}
