.background-image {
    width: 100%;
    padding-top: 8%;
}

.ind-apartment-bottom {
  border-top: 1px solid black;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.ind-apartment-bottom h2 {
    font-size: 30px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 8px;
}

.ind-apartment-bottom, p {
    padding-right: 1rem;
    align-items: center;
}
ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
li {
  padding: 0.4rem;
  padding-inline-end: 4ch;
}
.apartment-layout {
    width: 30vw;
    margin: 2rem;
    background-color: #f4f4f4;
}

.amentities-paragraphs {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

