  .apartment-image-swiper {
    width: 45vw;
    height: 70vh;;
  }
  
  .swiper-container {
          display: flex;
          justify-content: center;
          flex-direction: column;
          /* border-bottom: 1px solid black; */
          padding-bottom: 3rem;

  }
  .modal-swiper-div {
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      cursor: pointer;
      margin-bottom: 1rem;
  }
  
  .swiper-slide {
      display: flex;
      justify-content: center;
    }

  .swiper-wrapper {
     width: 45vw;
  }

    .swiper-button-next:before, .swiper-button-next:after  {
      color: #884d3b;
      background: white;
      padding: 1rem;
      border-radius: 4rem;
      margin-right: 3rem;
      width: 2vw;
      height: 2vh;

  
    }
    .swiper-button-prev:after,  .swiper-button-prev:before {
      color: #884d3b;
      background: white;
      padding: 1rem;
      border-radius: 4rem;
      margin-left: 3rem;
     
    }

  
