  }
  
  @media only screen and (max-width:  1438px) {
    .ind-apartment-info {
      align-items: center;
      /* border-bottom: 1px solid black; */
      width: 41vw;

    }

    .ind-apartment {
      display: flex;
    flex-direction: column;
    align-items: center;
    }

    .ind-apartment-bottom {
      align-items: center;
    }
    .ind-apartment-top {
      width: 100%;
      align-items: center;
    }
    .swiper-container {
